input[type="file"] {
  display: none !important; }

body {
  background-color: #f2f3f7;
  overflow-y: scroll;
  color: #666666; }

.main-title {
  padding-left: 15px;
  border-left: 3px solid #337ab7; }

.com-list {
  margin: 30px auto 20px auto; }
  .com-list > .list-group-item {
    padding-top: 20px;
    padding-bottom: 20px;
    border-left: 0;
    border-right: 0; }
    .com-list > .list-group-item .date {
      color: #969696; }
    .com-list > .list-group-item > span.title {
      margin-left: 7px; }
    .com-list > .list-group-item:first-child {
      border-top-right-radius: 0;
      border-top-left-radius: 0; }
    .com-list > .list-group-item:last-child {
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0; }
  .com-list .list-head {
    background-color: #E6E6E6;
    margin-left: -20px;
    margin-right: -20px;
    padding-left: 30px;
    padding-right: 35px; }

.attachment-list {
  margin-top: 10px; }
  .attachment-list > .list-group-item > a.text-danger {
    cursor: pointer; }

.progress-wrap .progress {
  margin: 5px 0; }

.normal-wrap {
  margin-top: 30px;
  margin-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px; }

.projectList {
  border: 1px solid #E8E8E8;
  margin-top: 30px; }
  .projectList thead {
    background-color: #E6E6E6; }
    .projectList thead .h-left {
      padding-left: 39px; }
    .projectList thead .h-right {
      float: right;
      padding-right: 40px; }
  .projectList tbody tr td .state-wrap {
    display: inline-block;
    background-color: #fff;
    border: 1px solid #000;
    border-radius: 2px;
    margin-right: 10px;
    font-size: 14px;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 1px;
    padding-bottom: 1px; }
  .projectList tbody tr td.time-wrap {
    text-align: right;
    vertical-align: middle; }

.form-horizontal .control-label {
  text-align: right;
  margin-bottom: 0;
  padding-top: 7px;
  font-weight: normal;
  text-align: left;
  width: 13%; }

.form-horizontal .half-width {
  width: 600px; }

.form-group {
  margin-bottom: 30px; }

.form-control {
  -webkit-box-shadow: none;
  box-shadow: none; }

form {
  padding-left: 50px;
  padding-right: 50px; }

.f-l {
  float: left; }

.f-r {
  float: right; }

.ta-c {
  text-align: center; }

.p-a {
  position: absolute; }

.p-r {
  position: relative; }

.btn-primary {
  background-color: #0074D3;
  border: none; }
  .btn-primary:hover {
    background-color: #1276D0;
    color: #ffffff; }
  .btn-primary:active {
    background-color: #1276D0;
    color: #ffffff; }

.btn-primary:active:focus {
  color: #fff;
  background-color: #1276D0;
  border-color: none; }

.my-btn {
  width: 100px;
  height: 40px;
  font-family: MicrosoftYaHei;
  font-size: 14px;
  color: #FFFFFF;
  border: none; }
  .my-btn:focus {
    outline: none;
    color: #ffffff; }

.btn-mb {
  margin-top: -5px; }

.submit-btn {
  background-color: #1F6BA9;
  border: none; }
  .submit-btn:hover {
    background-color: #2171B2;
    color: #ffffff; }
  .submit-btn:active {
    background-color: #2171B2;
    color: #ffffff; }
  .submit-btn:focus {
    background-color: #2171B2;
    color: #ffffff; }

.draft-btn {
  color: #ffffff;
  background-color: #FDAD03;
  border: none; }
  .draft-btn:hover {
    background-color: #D59203;
    color: #ffffff; }
  .draft-btn:active {
    background-color: #D59203;
    color: #ffffff;
    border: none; }

.cancel-btn {
  background-color: #F86A50;
  border: none;
  width: 60px; }
  .cancel-btn:hover {
    background-color: #C8503A;
    color: #ffffff; }
  .cancel-btn:active {
    background-color: #C8503A;
    color: #ffffff; }
  .cancel-btn:focus {
    background-color: #C8503A;
    color: #ffffff; }

.btn-mr {
  margin-right: 20px;
  border: none; }

.table-handle thead {
  background-color: #E6E6E6; }

.table-handle tbody tr td {
  padding: 15px; }

.table-handle .form-group {
  margin-bottom: 0; }

.business-label {
  background-color: #969696;
  padding-top: 12px;
  padding-bottom: 12px;
  font-weight: normal;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 15px; }

.staff-max {
  text-align: left;
  padding-left: 18px !important; }

.label {
  font-weight: normal;
  background-color: #fff;
  color: #51AAD6;
  letter-spacing: -0.32px;
  border: 1px solid #51AAD6;
  border-radius: 2px;
  padding: .1em .5em .1em; }

.label-danger {
  border: 1px solid #E84C4C;
  color: #E84C4C; }

label.col-sm-2.control-label {
  width: 145px; }



